.monetize_container {
  position: relative;
  background-color: #e5fcf5;
  display: flex;
  flex-direction: column;
  border: solid 1px black;
}
@media (max-width: 600px) {
  .monetize_container {
    height: 100vh;
  }
}

.body_container {
  position: relative;
  flex: 1 1 auto;
  text-align: center;
  padding-block-start: 126px;
  padding-block-end: 195px;
  padding-inline: 80px;
}
@media (max-width: 900px) {
  .body_container {
    padding-block-start: 165px;
    padding-inline: 25px;
    padding-block-end: 195px;
  }
}

.body_title {
  font-size: 100px;
  line-height: 110px;
  padding-block-end: 20px;
  padding-block-start: 105px;
}
@media (max-width: 900px) {
  .body_title {
    font-size: 40px;
    line-height: 45px;
    padding-block-end: 15px;
    padding-block-start: 65px;
  }
}

.body_paragraf {
  font-family: Nunito Sans, sans-serif;
  font-size: 30px;
  font-weight: 400;
  padding-block-end: 40px;
}
@media (max-width: 900px) {
  .body_paragraf {
    font-size: 22px;
    padding-block-end: 20px;
  }
}

.menu_item_link--width {
  font-size: 32px;
}
@media (max-width: 900px) {
  .menu_item_link--width {
    font-size: 16px;
  }
}

.body_link {
  padding-block-end: 195.5px;
}

.menu_item_link--width {
  display: inline-block;
  width: 100%;
}